import { ComponentChildren, JSX, Ref } from 'preact';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { usePress } from '../hooks/UNSAFE_usePress';
import { useHover } from '../hooks/UNSAFE_useHover';
import { classNames } from '../utils/UNSAFE_classNames';
import { forwardRef } from 'preact/compat';
import type { DimensionProps } from '../utils/UNSAFE_interpolations/dimensions';
import { dimensionInterpolations } from '../utils/UNSAFE_interpolations/dimensions';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';
import { getClientHints } from '../utils/PRIVATE_clientHints';

type ButtonElementDetails =
  | { type: 'button' }
  | { type: 'a'; href: string; target?: string; rel?: string };
const NullFunction = () => null;

type IntrinsicProps = Pick<JSX.HTMLAttributes<HTMLElement>, 'autofocus' | 'title'>;

export type Props = IntrinsicProps &
  DimensionProps & {
    /**
     *The default slot is the button's text label. The oj-c-button element accepts plain text or DOM nodes as children for the default slot."
     */
    children?: ComponentChildren;

    /**
     * Specifies that the button element should be disabled.
     */
    isDisabled?: boolean;

    /**
     * Indicates in what states the button has chrome (background and border).
     *     "borderless"': "Borderless buttons are the least prominent variation.
     * Borderless buttons are useful for supplemental actions that require minimal emphasis.",
     *     "outlined"': "Outlined buttons are salient, but lighter weight than
     * solid buttons. Outlined buttons are useful for secondary actions.",
     *     "solid"': "Solid buttons stand out, and direct the user's attention to the
     *  most important actions in the UI.",
     *     "callToAction"': "A Call To Action (CTA) button guides the user to take or
     * complete the action that is the main goal of the page or page section. There
     * should only be one CTA button on a page at any given time.",
     *     "danger"': "A Danger button alerts the user to a dangerous situation.",
     */
    variant?: 'borderless' | 'outlined' | 'solid' | 'callToAction' | 'danger';

    /**
     * size
     */
    size?: 'sm' | 'md' | 'lg';

    /**
     * edge
     */
    edge?: 'none' | 'bottom';

    /**
     * accessibleLabel
     */
    accessibleLabel?: string;

    /**
     * styling
     */
    styling?: 'default' | 'min' | 'unstyled';

    /**
     * Triggered when a button is clicked, whether by keyboard, mouse, or touch events.
     */
    onAction?: () => void;

    /**
     * Indicate element type and associated details
     */
    elementDetails?: ButtonElementDetails;
  };

const compStyles = {
  base: cssProps`${{
    alignItems: 'center',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-bg-color)',
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-color)',
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-radius)',
    borderStyle: 'solid',
    borderWidth: '1px',
    boxSizing: 'border-box',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-color)',
    cursor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-cursor-clickable)',
    display: 'inline-block',
    fontFamily: 'inherit',
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-font-size)',
    fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-font-weight)',
    height: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-height)',
    justifyContent: 'center',
    minWidth: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-height)',
    overflowX: 'hidden',
    paddingInlineStart: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-to-edge-padding)',
    paddingInlineEnd: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-to-edge-padding)',
    paddingTop: 0,
    paddingBottom: 0,
    pointerEvents: 'auto',
    textOverflow: 'ellipsis',
    verticalAlign: 'top',
    WebkitTapHighlightColor: 'rgba(0,0,0,0)',
    whiteSpace: 'nowrap',

    // @ts-ignore
    '&:active:enabled': {
      backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-bg-color-active)',
      borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-color-active)',
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-color-active)'
    },
    '&:focus': {
      outlineStyle: 'dotted',
      outlineOffset: '1px',
      outlineWidth: '1px',
      outlineColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-focus-border-color)'
    },
    '@supports selector(:focus-visible)': {
      '&:focus': {
        /* Remove the focus indicator on mouse-focus for browsers
           that do support :focus-visible */
        outline: 'none'
      }
    },
    '&:focus-visible': {
      outlineStyle: 'dotted',
      outlineOffset: '1px',
      outlineWidth: '1px',
      outlineColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-focus-border-color)'
    }
  }}`,

  hover: cssProps`${{
    // @ts-ignore
    '&:not(:active)': {
      backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-bg-color-hover)',
      borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-color-hover)',
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-color-hover)'
    }
  }}`,

  pseudohover: cssProps`${{
    // @ts-ignore
    '@media(hover: hover)': {
      '&:hover:enabled:not(:active)': {
        backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-bg-color-hover)',
        borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-color-hover)',
        color: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-color-hover)'
      }
    }
  }}`,

  unstyled: cssProps`${{
    boxSizing: 'border-box',
    cursor: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-cursor-clickable)',
    height: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-height)'
  }}`,

  min: cssProps`${{
    paddingInlineStart: '0',
    paddingInlineEnd: '0'
  }}`,

  disabled: cssProps`${{
    pointerEvents: 'none',
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-bg-color-disabled)',
    borderColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-border-color-disabled)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-button-text-color-disabled)'
  }}`,

  bottom: cssProps`${{
    borderLeftWidth: '0',
    borderRightWidth: '0',
    borderBlockEndWidth: '0',
    width: '100%',
    borderTopLeftRadius: '0',
    borderTopRightRadius: '0'
  }}`
};

const interpolations = [...Object.values(dimensionInterpolations)];

const styleInterpolations = mergeInterpolations<Props>(interpolations);

const clientHints = getClientHints();
// :active only works on IOS devices if a touch handler exists
const iosProps = clientHints.platform === 'ios' ? { ontouchstart: function () {} } : {};
const isHybrid = getClientHints().isHybrid;

export const BaseButton = forwardRef(
  (
    {
      variant = 'outlined',
      isDisabled = false,
      size = 'md',
      elementDetails = { type: 'button' },
      styling = 'default',
      edge = 'none',
      accessibleLabel,
      ...props
    }: Props,
    ref?: Ref<HTMLButtonElement> & Ref<HTMLAnchorElement>
  ) => {
    const { pressProps } = usePress(props.onAction ? props.onAction : NullFunction, {
      isDisabled
    });
    const { hoverProps, isHover } = useHover({ isDisabled: isHybrid });
    const { class: cls, ...styles } = styleInterpolations(props);
    const rootStyles = classNames([
      styling === 'default' && compStyles.base,
      styling === 'unstyled' && compStyles.unstyled,
      (styling === 'min' && compStyles.base) || compStyles.min,
      `oj-c-button-${variant}`,
      `oj-c-button-${size}`,
      isDisabled && compStyles.disabled,
      edge === 'bottom' && compStyles.bottom,
      !isHybrid && compStyles.pseudohover,
      isHybrid && isHover && compStyles.hover
    ]);
    const { type: elementType = 'button', ...elementProps } = { ...elementDetails };
    const role = elementType === 'button' ? { type: 'button' } : { role: 'link' };
    const tooltip = props.title || accessibleLabel;
    const ElementType = elementDetails.type;

    return (
      <ElementType
        ref={ref}
        disabled={isDisabled}
        class={`${rootStyles} ${cls}`}
        style={styles}
        autofocus={props['autofocus']}
        title={tooltip}
        tabIndex={isDisabled ? -1 : 0}
        aria-label={accessibleLabel}
        {...elementProps}
        {...role}
        {...pressProps}
        {...iosProps}
        {...hoverProps}>
        {props.children}
      </ElementType>
    );
  }
);
